<template>
  <div id="app">
    <!-- header -->
    <div class="w3-bar w3-white w3-top w3-card">
      <div class="w3-bar-item">
        <img src="@/assets/litemotion-logo.png" style="height:48px;">
      </div>
      <div class="w3-bar-item cursor w3-hide-small" @click="changeTab('home')">
        <div class="w3-padding menu-text">{{$t('WE')}}</div>
        <div v-if="tab=='home'" class="w3-red" style="height:4px;"></div>
      </div>
      <div class="w3-bar-item cursor w3-hide-small" @click="tab='blog'">
        <div class="w3-padding menu-text">{{$t('BLOG')}}</div>
        <div v-if="tab=='blog'" class="w3-red" style="height:4px;"></div>
      </div>
      <div class="w3-bar-item cursor w3-hide-small" @click="changeTab('support')">
        <div class="w3-padding menu-text">{{$t('SUPPORT')}}</div>
        <div v-if="tab=='support'" class="w3-red" style="height:4px;"></div>
      </div>
      <div class="w3-bar-item w3-right">
        <button class="w3-btn w3-tiny" v-if="lang!='en'" @click="changeLang('en')">ENG</button>
        <button class="w3-btn w3-tiny" v-if="lang!='zh-tw'" @click="changeLang('zh-tw')">繁體</button>
      </div>
    </div>
    <!-- header end -->
    <div style="height:64px;" class="w3-hide-small"></div>
    <div style="height:118px;" class="w3-hide-large w3-hide-medium"></div>

    <router-view/>
  </div>
</template>
<script>
export default {
  name: 'app',
  data(){return{
    tab:'home',
    lang:'en',
    showingWhatWeDo:true,
  }},
  created(){
    this.$util.detectLang()
    this.lang = this.$i18n.locale
  },
  beforeUpdate(){
    if(this.$route&&this.$route.name) {
      this.changeTab(this.$route.name)  
    }
  },
  methods:{
    changeTab(tab) {
      this.tab = tab
      if(this.$route.name!= tab) this.$router.push(tab)
    },
    changeLang(lang){
      this.$util.changeLang(lang)
      this.lang = this.$i18n.locale
    }
  }
}
</script>
<style>
@import './assets/w3.css';

.cursor {cursor:pointer;}
.menu-text {min-width:75px;}
</style>
