import Vue from 'vue'
import App from './App.vue'
import router from './router'
import VueI18n from 'vue-i18n'

import util from '@/components/util' // add to test custom plugin
import i18n from '@/lang';

import axios from 'axios'
import VueAxios from 'vue-axios'


const axiosIns = axios.create({withCredentials:true})
//console.log(axios.CancelToken)
Vue.use(VueAxios, axiosIns)

//Vue.use(VueAxios, axios)

Vue.use(util)// add to test custom plugin
Vue.use(VueI18n)

new Vue({
  router,
  i18n,
  render: h => h(App)
}).$mount('#app')
