import i18n from '../lang';

const util = {

  install(Vue,options){
    Vue.prototype.$util = {
      screenSize(){
        let w  = window.innerWidth
        let h = window.innerHeight
        let n = 'small'
        if(w>=993) n= "large"
        if(w>=601) n= "medium"
        return {w:w,h:h,name:n}
      },
      isEmail(s){
        return (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(s))
      },
      detectLang(){
        //console.log('detectLang..')
        // using localStorage 
        let locale = localStorage['locale']
        let locales = Object.keys(i18n.messages)
        if(locale&&(locales.indexOf(locale)>=0)) {
          this.changeLang(locale)
          return;
        }
        // then using navigator.language
        locale = navigator.language
        if(locale&&(locales.indexOf(locale)>=0)) {
          this.changeLang(locale)
          return;
        }
        // then user acceptable languages
        let languages = navigator.languages
        if(languages){
          languages.map(lang=>{
            lang = (lang||"").toLowerCase()
            if(locale&&(locales.indexOf(lang)>=0)) {
              this.changeLang(lang)
              return;
            }
          })
          console.log(languages,'no acceptable languages')
          let languages_str = languages.join(" ")
          locales.map(locale=>{
            //console.log('finding major language of',locale)
            let locale_major = locale.split("-")[0]
            //console.log('major:',locale_major)
            let m = languages_str.match(new RegExp(locale_major,"i"))
            if(m) {
              console.log('using lang:',locale)
              this.changeLang(locale)
              return;
            }
          })
          
        }
        

      },
      changeLang(locale){
        locale = (locale||"").toLowerCase()
        let locales = Object.keys(i18n.messages)
        if(locales.indexOf(locale)>=0) {
          i18n.locale = locale
          localStorage['locale'] = locale
          return;
        }
      }
    }
    
  }
}


export default util