<template>
  <div class="w3-border w3-border-light-gray w3-white">
    <div style="display:inline-block">
      <canvas :width="canvasWidth" :height="canvasHeight" :ref="canvasRef"></canvas>
    </div>
    <div style="display:inline-block;vertical-align:top">
      <div class="w3-padding w3-left-align">
        <a href="javascript://" @click="drawCaptcha" class="w3-text-blue w3-small"> <i class="fa fa-refresh"></i> {{$t('change-image')}}</a>
      </div>
      <div class="w3-padding">
        {{$t('type-the-code')}}: <input v-model="code">
      </div>
    </div>
  </div>
</template>
<script>
const captcha = function(canvasId,options={}) {
	let gg = Math, aa = parseInt, hh = gg.random; 
	let zzc = function(x) {return hh()*aa(x)}
	let zzd = function(x,y) {return (aa(zzc(x)+247)%y)}
	//var randomrect = function() { return {x:zzd(1000,249),y:zzd(1000,139),w:zzd(1000,249),h:zzd(1000,139)}}
	let ab = function() { return {x:zzd(1000,9),y:zzd(1000,39),w:zzd(1000,249),h:zzd(1000,139)}}
	
	const randomstr = options.randomStr||"Y003Jha11jGxG3EMR6CCT7BSvicVyJkVvYU6TFc6NHORLGR56+0CpqJONi16nUW01o36Q12nMswwmFSSOCAc1"
	let randompos = [1,1,1,1,1,1]
	let w = options.width||240
  let h = options.height||140
  let maxSize = (w-1)/randompos.length
  let angleOption = [0,5,10,-4,-8]

  //let sizes = [48,56,72] // sizes options of the chars of code
  let sizes = [maxSize,maxSize*1.7,maxSize*2]
  //console.log(sizes)
  let fonts = ['"arial black"','monospace','"Comic Sans MS"']
	let angles = [0,0,2,4,6,-2,-4,-6]
	let colors = ["rgba(0,0,0,0.5)","rgba(255,0,0,0.5)","rgba(255,255,2,0.9)","rgba(2,2,255,0.5)"]
	let code = ""
	
  // randomly the char of code from randomstr 
	randompos = randompos.map(function(a,i){
		return parseInt(randomstr.length* Math.random())
  })
  angles = angles.map(function(a,i){
    let idx = parseInt(Math.random()*237) % angleOption.length
    //console.log(idx)
    return angleOption[ idx]
  })
  //console.log(angles)
  //console.log('randompos')
  //console.log(randompos)
	function codestr() {
		let s = ""
		randompos.map(function(i){
			s+=randomstr[i]
		})
		code = s
		//console.log(s)
	}
  codestr()
  //console.log('code created by codestr',code)
  
	var can = (canvasId.tagName)?canvasId:document.getElementById(canvasId)
	var ctx = can.getContext("2d")
  
  // draw the code 
	ctx.fillStyle="#fff"
	ctx.fillRect(0,0,w,h)
	
	let rr = ab()
	ctx.fillStyle="rgba(30,200,200,0.5)"
	ctx.fillRect(rr.x,rr.y,rr.w,rr.h)
	
	rr = ab()
	ctx.fillStyle="rgba(200,200,255,0.5)"
	ctx.fillRect(rr.x,rr.y,rr.w,rr.h)
	
	rr = ab()
	ctx.fillStyle="rgba(200,5,200,0.5)"
  ctx.fillRect(rr.x,rr.y,rr.w,rr.h)
  
  
	rr = ab()
	ctx.fillStyle="rgba(200,255,20,0.5)"
	ctx.fillRect(rr.x,rr.y,rr.w,rr.h)
	
	var lastX = 10
	var lastY = 85
	for(var i=0;i<code.length;i++) {
		var size = parseInt(Math.random()*100)% sizes.length
		var font = parseInt(Math.random()*100)% fonts.length
		var angle= parseInt(Math.random()*100)% angles.length
    var color= parseInt(Math.random()*100)% colors.length
    
    //console.log(code[i],'size:',sizes[size],'font:',fonts[font],'angle:',angles[angle], 'lastX:',lastX)
		ctx.font = sizes[size]+'px ' + fonts[font]
		ctx.rotate(angles[angle]*Math.PI/180);
		ctx.fillStyle=colors[color]
		ctx.fillText(code[i],lastX,lastY)
		ctx.rotate(-angles[angle]*Math.PI/180);
		lastX += sizes[size]*0.5 //-25
		//console.log(code[i],ctx.font,lastX,lastY,size,font)
  }
  // end draw code

	function check(uc) {
		if(!uc) return false
		if(!code) return false
		if(uc.toLowerCase()==code.toLowerCase()) return true
		else return false	
	}
	
	return function(sss){
		return check(sss);
	}
}

export default {
  name:"captcha",
  props:['width','height','randomStr'],
  data(){return{
    code:null,
    canvasRef:"zzz",
    canvasWidth:200,
    canvasHeight:100,
    captcha:null,
    captchaOptions:null
  }},
  created(){
    if(this.width) this.canvasWidth = this.width
    if(this.height) this.canvasHeight = this.height
    this.captchaOptions = {
      width:this.canvasWidth,
      height:this.canvasHeight
    }
    if(this.randomStr) this.captchaOptions.randomStr = this.randomStr
  },
  mounted(){
    this.drawCaptcha()
  },
  watch:{
    code(newVal,oldVal){
      this.checkCaptcha(newVal)
    }
  },
  methods:{
    drawCaptcha(){
      if(this.$refs&&this.$refs[this.canvasRef]) {
        let canv = this.$refs[this.canvasRef]
        this.captcha = captcha(canv,this.captchaOptions)
        this.code = null
      } 
    },
    checkCaptcha(s){
      if(this.captcha) {
        let b = this.captcha(s)
        if(b) this.$emit("pass",true)
        if(!b) this.$emit("fail",false)
      }
    }
  }
}
</script>