import Vue from 'vue'
import VueI18n from 'vue-i18n'

// 自訂語言檔
import en from './en.js'
import zhTW from './zh-tw.js'

// 使用插件
Vue.use(VueI18n)

// 取得預設語系
//const locale = localStorage.getItem('locale') || 'tw'

// 建立 VueI18n 實體
const i18n = new VueI18n({
  locale:'en',
  messages: { 'en':en, 'zh-tw':zhTW }
})

export default i18n